import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ApiCall {

  appid = "";
  rooturl = "https://64q0oh6zui.execute-api.us-east-1.amazonaws.com/testv1/";

  constructor(public http: HttpClient) {
    this.http = http;
  }

  getUserProfile(term:string) {

      let url = this.rooturl+"getUserInfo?userName="+term;
      //headers.append('Authorization', 'Basic '+ btoa(this.appid + ':' + this.appid));
      return this.http.get(url)
          
  }
    
    getRecommendation(payload: any){
        let url = this.rooturl+"getUserRecommendation"
         var headers = {
        "content-type": "application/json",
      }
        return this.http.post(url, payload, {headers:headers})
    }
    
    getFitnessRecommendation(payload: any){
        let url = this.rooturl+"getFitnessRecommendation"
         var headers = {
        "content-type": "application/json",
      }
        return this.http.post(url, payload, {headers:headers})
    }
    
    updateRatings(payload: any){
        let url = this.rooturl+"updateRatings"
         var headers = {
        "content-type": "application/json",
      }
        return this.http.post(url, payload, {headers:headers})
    }
    
    cognitoGetAuth(url: string, payload: string, base64Encoded: string){

            var headers = {
            "authorization": base64Encoded,
            "content-type": "application/x-www-form-urlencoded",
            "cache-control": "no-cache"
          }
      return this.http.post(url, payload, {headers:headers})
    }
    
    getUserInfo(url: string, accessToken: string){
      var headers = {
    "authorization": accessToken,
    "content-type": "application/x-www-form-urlencoded",
    "cache-control": "no-cache"
  }
      return this.http.get(url, {headers:headers})
    }
    
    
    fitbitGetActivity(url: string, access_token: string){
        var headers = {
            "authorization": access_token,
            "content-type": "application/x-www-form-urlencoded"
          }
        
      return this.http.get(url, {headers:headers})
        
    }
    
    fitbitLogFood(url: string, payload: any, access_token: string){
        var headers = {
            "authorization": access_token,
            "content-type": "application/x-www-form-urlencoded"
          }
        
      return this.http.post(url, payload, {headers:headers})
        
    }
    
    //To database
    pushUserProfile(profile: any){
        let url = this.rooturl+"updateUserInfo";
        var headers = {
    "content-type": "application/json",
  }
        return this.http.post(url, profile, {headers:headers})
    }
    
    
    fallbackApi(payload: any){
        var url = this.rooturl+"getEFRecommendation"
         var headers = {
        "content-type": "application/json",
      }
        return this.http.post(url, payload, {headers:headers})
        
    }
    
    
    
    

}


